import React, { useState } from 'react'
import { Link } from 'react-router-dom';

const Header = (props) => {
  /*const [isMenuActive, setIsMenuActive] = useState(false);*/
  const [isSnbActive, setIsSnbActive] = useState(false);
  const [isHeaderFixed, setIsHeaderFixed] = useState(false);

  const handleSnbToggle = () => {
    setIsSnbActive(!isSnbActive);
  };

  const handleScroll = () => {
    const scrollTop = window.pageYOffset;
    setIsHeaderFixed(scrollTop > 0);
  };

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);


  return (
    <header
      id="header__wrap"
      className={`${props.attr} ${isHeaderFixed ? 'fixed' : ''}`}
      role="heading"
      aria-level="1"
    >
      
      <div className="header__inner">
        <div className="header__top container">
          <h1 className="header__logo">
              <img src={require("../../../assets/images/main02.png")} />
          </h1>
          <div className="header__member">
            <Link to="/Loginform"></Link>
            <Link to="/Loginform"></Link>
            <Link to="/" className="lang">
              <div className="select1 language">
                <select className="select-type">
                  <option value="/main?locale=ko"></option>
                  <option value="/main?locale=en"></option>
                </select>
              </div>
            </Link>
          </div>
        </div>
     

      <nav className="header__menu">
        <ul>
          <li>
            <Link to="/"></Link>
          </li>
      
          <li>
            <Link to="/market"></Link>
          </li>

          <li>
            <Link to="/festival"></Link>
          </li>
        </ul>

          <Link
            
            className={`snb-more ${isSnbActive ? '' : 'active'}`}
            onClick={handleSnbToggle}
          >
             
          </Link>
        </nav>

        <div className={`snb-wrap ${isSnbActive ? '' : 'active'}`}>
          <div className="inner-box">
            <div className="mnu-wrap">

                <div className="mnu-box">
                  <Link> </Link>
                  <ul>
                      <li>
                        <Link to="/RegistMenu"></Link>
                      </li>
                      <li>
                        <Link to="/"></Link>
                      </li>
                      <li>
                        <Link to="/"></Link>
                      </li>
                  </ul>
                </div>
              </div>

              <div className="mnu-box">
                <Link></Link>
                  <ul>
                    <li>
                      <Link to="/"></Link>
                    </li>
                    <li>
                      <Link to="/"></Link>
                    </li>
                    <li>
                      <Link to="/">  </Link>
                    </li>
                  </ul>
              </div>
            </div>
        </div>
      </div>
    </header>
  );
};

export default Header;